
.btn {
  width: 40px;
  height: 40px;
}
.btn.direction {
  position: fixed;
  text-align: center;
  top: 47%;
}
.btn.direction.right {
  right: .2em;
}
.btn.direction.left {
  left: .2em;
}
.arrow {
  font-size: 3em;
  font-weight: bold;
}
.arrow a {
  text-decoration: none;
  color: black;
}
.arrow a:hover {
  color: red;
}
@media (max-width: 500px) {
.btn.direction {
    top: 10%;
}
}
@media (max-width: 600px) {
.btn.direction.left {
    left: .1em;
}
.btn.direction.right {
    right: .1em;
}
}



