
h2 {
  font-size: 4em;
  font-weight: 300;
}
.portfolio-links {
  display: block;
  list-style-type: none;
  padding: 0;
}
@media (max-width: 600px) {
h2 {
    font-size: 2.5em;
}
}
@media (max-width: 500px) {
h2 {
    font-size: 2em;
}
}
.slide-left-enter-active,
.slide-right-enter-active {
  transition: all .2s ease;
}
.slide-left-leave-active,
.slide-right-leave-active {
  transition: all .2s ease;
}
.slide-right-enter {
  transform: translateX(-1200px);
}
.slide-right-leave-active {
  transform: translateX(1200px);
}
.slide-left-enter {
  transform: translateX(1200px);
}
.slide-left-leave-active {
  transform: translateX(-1200px);
}
.hide-enter-active {
  transition-delay: .3s;
}
.hide-leave-active {
  transition-delay: .2s;
}
.hide-enter {
  visibility: hidden;
}
.hide-leave-active {
  visibility: hidden;
}



